/* Main container styling */
.upload-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #000000;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
}

/* Gradient background for top section */
.upload-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40vh;
  background: linear-gradient(135deg, #ff70b5 0%, #5e82eb 100%);
  z-index: -1;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
}

/* Logo styling */
.logo-container {
  padding: 2rem;
  margin-bottom: 2rem;
}

.logo-container h1 {
  font-size: 5rem;
  font-weight: 900;
  line-height: 0.9;
  color: #ffffff;
  text-transform: uppercase;
  margin: 0;
}

.my-text {
  font-size: 4rem;
}

/* Content section */
.content-section {
  padding: 2rem;
}

/* Main headline */
.headline {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  color: #ffffff;
}

/* Compliance information */
.compliance-info {
  margin-bottom: 2rem;
}

.compliance-info p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.compliance-info ul {
  list-style-type: none;
  padding-left: 1rem;
}

.compliance-info li {
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  position: relative;
  padding-left: 1.5rem;
}

.compliance-info li::before {
  content: "â€¢";
  position: absolute;
  left: 0;
  color: #ffffff;
}

/* Form styling */
.upload-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 2rem;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 8px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

label {
  font-weight: 600;
  color: #ffffff;
}

input[type="email"],
input[type="file"] {
  padding: 0.75rem;
  border: 1px solid #444;
  border-radius: 4px;
  font-size: 1rem;
  background-color: rgba(255, 255, 255, 0.9);
}

input[type="email"]:focus {
  outline: none;
  border-color: #8e44ad;
  box-shadow: 0 0 0 2px rgba(142, 68, 173, 0.2);
}

.file-name {
  margin-top: 0.5rem;
  font-size: 0.9rem;
  color: #cccccc;
}

/* Message styling */
.error-message {
  padding: 0.75rem;
  background-color: rgba(231, 76, 60, 0.2);
  color: #ff6b6b;
  border-left: 4px solid #e74c3c;
  border-radius: 4px;
}

.success-message {
  padding: 0.75rem;
  background-color: rgba(39, 174, 96, 0.2);
  color: #2ecc71;
  border-left: 4px solid #27ae60;
  border-radius: 4px;
}

/* Submit button with gradient */
.submit-button {
  padding: 1rem 1.5rem;
  background: linear-gradient(90deg, #5e3bee 0%, #ff70b5 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  text-align: center;
  letter-spacing: 1px;
}

.submit-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.submit-button:disabled {
  background: linear-gradient(90deg, #666666 0%, #999999 100%);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Processing status */
.processing-status {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
}

.processing-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top: 4px solid #ffffff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-top: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Disclaimer */
.disclaimer {
  text-align: center;
  margin-top: 2rem;
  font-size: 0.9rem;
  color: #cccccc;
  font-weight: 600;
}

/* Responsive design */
@media (max-width: 768px) {
  .logo-container h1 {
    font-size: 3.5rem;
  }
  
  .my-text {
    font-size: 3rem;
  }
  
  .headline {
    font-size: 1.5rem;
  }
}
